/* Обнуление */
* {
padding: 0;
margin: 0;
border: 0;
}
*,*:before,*:after {
-moz-box-sizing: border-box;
-webkit-box-sizing: border-box;
box-sizing: border-box;
}
:focus,:active,a:focus,a:active {
outline: none;
}
html, body {
height:100%;
width:100%;
line-height:1;
font-size:16px;
-ms-text-size-adjust:100%;
-moz-text-size-adjust:100%;
-webkit-text-size-adjust:100%;
}
input, button, textarea {
font-family:inherit;
}
input::-ms-clear {
display: none;
}
button { 
cursor: pointer;
}
button::-moz-focus-inner {
padding:0;
border:0;
}
a,a:hover,a:visited {
text-decoration: none;
}
ul li {
list-style: none;
}
img {
vertical-align: top;
}
h1, h2, h3, h4, h5, h6 {
font-size:inherit;
font-weight: inherit;
}
/*--------------------*/